<template>
  <div>
 

<el-row :gutter="20">
   
</el-row>
<el-row :gutter="20">
    <el-col :span="7">
        <el-input placeholder="请输入内容" v-model="rParams.areaKey">
           <template slot="prepend">areaKey:</template>
        </el-input>
    </el-col>
    <el-col :span="7">
        <el-input placeholder="请输入内容" v-model="rParams.taskType">
           <template slot="prepend">taskType:</template>
        </el-input>
    </el-col>
    <el-col :span="6">
         <el-select v-model="rParams.key" placeholder="请选择" value-key="rParams.key"	>
           <el-option
      v-for="oItem in keySelectList"
      :key="oItem.Key"
      :label="oItem.Val"
      :value="oItem.Key" @change="test(rParams.key)">
          </el-option> 
    </el-select>
    </el-col>
    <el-col :span="4">
        <el-checkbox v-model="rParams.saveImg">备选项</el-checkbox>
    </el-col>
  <el-col :span="6">
     <el-input placeholder="请输入内容" v-model="rParams.x">
      <template slot="prepend">x:</template>
    </el-input>
  </el-col>
  <el-col :span="6">
     <el-input placeholder="请输入内容" v-model="rParams.y">
      <template slot="prepend">y:</template>
    </el-input>
  </el-col>
  <el-col :span="6">
     <el-input placeholder="请输入内容" v-model="rParams.w">
      <template slot="prepend">w:</template>
    </el-input>
  </el-col>
  <el-col :span="6">
     <el-input placeholder="请输入内容" v-model="rParams.h">
      <template slot="prepend">h:</template>
    </el-input>
  </el-col>

    <el-col :span="4">
        <el-button type="danger" v-if="rParams.cmd=='stop'"  @click="taskCtl();rParams.cmd=='start'">关闭</el-button>
        <el-button type="success" v-if="rParams.cmd=='start'"  @click="taskCtl();rParams.cmd=='stop'">开启</el-button>
  </el-col>

   
</el-row>

<el-row>
  
</el-row>
<el-button v-for="item in CommonQueryList" :label="item.val" :key="item.key" @click="commonQuery(item.key)" round>{{item.val}}</el-button>
       <el-button type="warning" icon="el-icon-star-off" circle style="float: right; margin-top: -30px;" size="min" @click="verifyKey"></el-button> 

       <div>{{last}}</div>
  </div>
</template>

<script>
  var _this;

  export default {
    components:{},
    data:function () {
      return{
        CommonQueryList : [
  {key:'W_saveGameArea',val:"设置游戏区域"},
  {key:'W_saveRect',val:"设置区域"},
  {key:'W_savePoint',val:"设置点位"},
  {key:'W_saveImage',val:"保存图片"},
  {key:'W_verifyKey',val:"验证Key"},
  {key:'W_verifyRect',val:"验证Rect"},
  ],
        keySelectList:[{Key:1,Val:1}],
        last:{},
        rParams:{
               areaKey:"",
               taskType:0,
               key:"1",
               x:0,
               y:0,
               w:0,
               h:0,
               cmd:"start",
               saveImg:false
        }
      }
    },
    methods:{
       verifyKey:function(){
        _this.$http.get({api:"W_VERIFYKEY",params:{taskId:1,screen:"mini",rect:"mainCheckRect" }}).then(data=>{
          _this.last=data
        },(res)=>{   _this.$store.commit('showAlertv', {text: res}) });
       },
       commonQuery(apiKey){
         _this.$http.get({api:apiKey,params:this.rParams}).then(data=>{
          _this.last=data
        },(res)=>{   _this.$store.commit('showAlertv', {text: res}) });
       },
       taskCtl(){
        _this.$http.get({api:"W_taskCtl",params:_this.rParams}).then(data=>{
          if (_this.rParams.cmd=="start"){
            _this.rParams.cmd="stop"
          }else{
            _this.rParams.cmd="start"
          }
        },(res)=>{   _this.$store.commit('showAlertv', {text: res}) });
       },
       test(info){
console.log(info)
       }
    },
    watch:{
      "rParams.key"(){
        console.log(_this.rParams.key)
      }
    },

    created:function () {
      _this=this;
    },
    mounted:function () {
         _this.$http.get({api:"W_KEYLIST",params:{}}).then(data=>{



          _this.keySelectList=data.content.keyList
          _this.rParams.key=data.content.keyList[0].Key
        },(res)=>{   _this.$store.commit('showAlertv', {text: res}) });
    }
  }
</script>



